  color: rgb(255, 255, 255);
  border: 1px white solid;
  margin-bottom: 3px;
  backdrop-filter: blur(10px);
}

.btn4:hover {
  background: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
  border: 1px rgb(255, 255, 255) solid;
  margin-bottom: 3px;
  backdrop-filter: blur(10px);
}

@media (max-width: 700px) {
  #seven {
    display: flex;
    flex-direction: column;
  }

  #main {
    background: #000;
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
}

.feature_links h1 {
  color: #000;
  font-size: 1.3rem;
  font-family: "rubik", serif;
}

.feature_links p {
  color: #000;
  font-size: 1rem;
  font-family: "poppins";
}

.feature_links {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 2rem auto;
  gap: 10px;
}

#rand {
  width: 100%;
  height: 100vh;
  background-image: url(https://tesla-cdn.thron.com/delivery/public/image/tesla/c960989c-3359-4caf-8cc7-afb07c372d6f/bvlatuR/std/4096x2560/Autopilot-Hero-Vision-Desktop);
  object-fit: cover;
  position: relative;
  background-position: center;
  background-repeat: no-repeat;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  display: flex;
  flex-wrap: wrap;
}

#rand p {
  margin-bottom: 20px;
  font-weight: 500;
  display: flex;
  flex-wrap: wrap;
}

#rand h1 {
  font-weight: 500;
  display: flex;
  flex-wrap: wrap;
}

.rand_info {
  position: absolute;
  left: 1rem;
  top: 8rem;
  text-align: left;
}

#advance_vid {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 1200px) {
  #advance_vid {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
  }
}

.advid img {
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  width: 100%;
  float: left;
  margin: 1rem auto;
  object-fit: cover;
}

.advid video {
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  width: 100%;
  float: left;
  margin: 1rem auto;
  object-fit: cover;
}

.adinfo h1 {
  text-align: left;
}

.adinfo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  text-align: left;
  float: right;
  margin: auto 2rem;
}

.adinfo button {
  padding: 0.2rem 9rem;
  background: #000;
  color: aliceblue;
  border: 1px solid transparent;
  border-radius: 20px;
}

@media (max-width: 500px) {
  .adinfo button {
    padding: 0.2rem 6rem;
    background: #000;
    color: aliceblue;
    border: 1px solid transparent;
    border-radius: 20px;
    font-size: 0.9rem;
  }
}

.adinfo button:hover {
  padding: 0.2rem 9rem;
  background: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
  border: 1px solid black;
}

@media (max-width: 768px) {
  .advid video {
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    width: 100%;
    margin: 1rem auto;
  }
}

.features_bottom > video {
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  width: 100%;
}

.features_top {
  text-align: left;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}
