* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.footer_img > img {
  width: 100%;
}

.foot_header > h1 {
  font-family: "jost", serif;
  font-weight: 700;
  margin-bottom: 3rem;
  font-size: 3rem;
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 3rem auto;
  max-width: 100%;
}

.foot_hero {
  display: flex;
  flex-direction: column-reverse;
}

.foot_topinfo {
  display: flex;
  gap: 1rem;
  justify-content: space-between;
  align-items: center;
}

.foot_topinfo button:nth-child(1) {
  padding: 0.5rem 15%;
  font-weight: 500;
  background: #000;
  color: aliceblue;
  border: 1px solid black;
  font-family: "jost", serif;
  margin: 2rem auto;
  border-radius: 50px;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  -ms-border-radius: 50px;
  -o-border-radius: 50px;
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
}

.foot_topinfo button:nth-child(2) {
  padding: 0.5rem 15%;
  font-weight: 500;
  background: #000;
  color: aliceblue;
  border: 1px solid black;
  font-family: "jost", serif;
  margin: 2rem auto;
  border-radius: 50px;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  -ms-border-radius: 50px;
  -o-border-radius: 50px;
}
.foot_topinfo button:nth-child(1):hover {
  padding: 0.5rem 15%;
  font-weight: 500;
  background: #00000000;
  color: rgb(0, 0, 0);
  border: 1px solid black;
  font-family: "jost", serif;
  margin: 2rem auto;
  border-radius: 50px;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  -ms-border-radius: 50px;
  -o-border-radius: 50px;
}

.foot_topinfo button:nth-child(2):hover {
  padding: 0.5rem 15%;
  font-weight: 500;
  background: #00000000;
  color: rgb(0, 0, 0);
  border: 1px solid black;
  font-family: "jost", serif;
  margin: 2rem auto;
  border-radius: 50px;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  -ms-border-radius: 50px;
  -o-border-radius: 50px;
}

@media (max-width: 480px) {
  .foot_topinfo button:nth-child(1) {
    padding: 0.2rem 10%;
    font-weight: 500;
    background: #000;
    color: aliceblue;
    border: 1.5px solid black;
    font-family: "jost", serif;
    margin: 2rem auto;
  }

  .foot_topinfo button:nth-child(2) {
    padding: 0.2rem 10%;
    font-weight: 500;
    background: rgb(255, 255, 255);
    border: 1.5px solid black;
    font-family: "jost", serif;
    margin: 2rem auto;
  }
}
@media (max-width: 480px) {
  .foot_topinfo button:nth-child(1) {
    padding: 0.2rem 7%;
    font-weight: 500;
    background: #000;
    color: aliceblue;
    border: 1.5px solid black;
    font-family: "jost", serif;
    margin: 2rem auto;
  }

  .foot_topinfo button:nth-child(2) {
    padding: 0.2rem 7%;
    font-weight: 500;
    background: rgb(255, 255, 255);
    border: 1.5px solid black;
    font-family: "jost", serif;
    margin: 2rem auto;
  }
}

.foot_top {
  display: flex;
  flex-direction: column;
}

.foot_bottom {
  width: 50%;
  color: rgb(106, 106, 106);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 1rem;
}

@media (max-width: 768px) {
  .foot_bottom {
    width: 95%;
    color: rgb(106, 106, 106);
  }
}

.foot_link {
  color: rgb(78, 78, 78);
  text-decoration: none;
  margin: 0.5rem 1rem;
}
.foot_link:hover {
  color: rgb(23, 23, 23);
  text-decoration: none;
  margin: 0.5rem 1rem;
  text-decoration: overline;
}

@media (max-width: 768px) {
  .footer_link {
    display: flex;
    flex-direction: column;
  }
}
