.gotobutton {

    position: fixed;
    right: 3rem ;
    bottom: 3rem;
    font-size: .7rem;
    background: rgba(161, 161, 161, 0.201);
    border-radius: 50%;
    padding: 0.6rem .8rem;
    cursor: pointer;
    color: rgba(240, 248, 255, 0.745);
    animation-name: topup;
    animation: 5s all infinite;

}
.gotobutton:hover {

    position: fixed;
    right: 3rem ;
    bottom: 3rem;
    font-size: .7rem;
    background: rgb(0, 0, 0);
    border-radius: 50%;
    padding: 0.6rem .8rem;
    cursor: pointer;
    color: rgb(255, 255, 255);
    /* transform: translate3d(0px, -4px , -5px) perspective(100px); */
    

}

@keyframes topup {
    
    0% {

        transform: translateY(0);
    }
    33% {

        transform: translateY(-15px);
    }
    66% {

        transform: translateY(15px);
    }
    0% {

        transform: translateY(0);
    }
}